import "./rate.scss";

$(function () {
  $(".rating__checkbox").on("click", function () {
    var elem = $(this).parent().parent().find(".rating__checkbox");

    //Ð£Ð´Ð°Ð»ÐµÐ½Ð¸Ðµ Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð° checked
    for (var i = 0; i < elem.length; i++) {
      $(elem[i]).attr("checked", false);
    }

    var star = [];

    //Ð£ÑÑ‚Ð°Ð½Ð¾Ð²ÐºÐ° Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð° checked Ð½Ð°Ð¶Ð°Ñ‚Ð¾Ð¼Ñƒ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñƒ
    $(this).attr("checked", true);

    for (var i = 0; i < elem.length; i++) {
      if ($(elem[i]).attr("checked")) {
        star[i] = 1;
      } else {
        star[i] = 0;
      }
    }

    //Ð—Ð°Ð¶Ð¸Ð³Ð°Ð½Ð¸Ðµ Ð²ÑÐµÑ… Ð·Ð²ÐµÐ·Ð´ Ð¾Ñ‚ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° Ñ Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð¾Ð¼ checked Ð´Ð¾ Ð½Ð°Ñ‡Ð°Ð»Ð°
    for (var i = elem.length; i >= 0; i--) {
      if (star[i] == 1 && i != 0) {
        star[i - 1] = 1;
      }
    }

    $(this)
      .parent()
      .parent()
      .find(".rating__star")
      .each(function (index) {
        if (star[index] == 1) {
          $(this).text("star");
        } else {
          $(this).text("star_border");
        }
      });
  });
});

